import React from 'react';
import './style.scss';
import { isEmptyObject, px } from '../../../Utils';

export default class GraphDisplayerMainUi extends React.Component {
    constructor(props) {
        super(props);

        this.graphClicked = this.graphClicked.bind(this);
    }

    getConfig() {
        return JSON.parse(this.props.config);
    }
    graphClicked(ev) {
        const { raiseGraphClicked } = this.props;
        const { coords, dataUrl } = this.getConfig();
        raiseGraphClicked({
            message: 'Graph clicked',
            coords,
            dataUrl
        });
    }

    render() {
        const config = this.getConfig();
        const noConfig = isEmptyObject(config);
        const { width, height, dataUrl } = config;

        const containerStyle = {
        };
        const imageStyle = {
            width: px(width),
            height: px(height),
            display: noConfig ? 'none' : 'block'
        }

        return (
            <div className='graph-displayer-main-ui'
                style={containerStyle}
                onClick={this.graphClicked}
                title={'GraphDisplayer'}
                >
                <img src={dataUrl} style={imageStyle} alt='' />
            </div>
        )
    }
}